          <el-input v-model="ruleForm.delivery_code" :disabled="true">
            <template slot="prepend">納品CD</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="project_name">
          <el-input v-model="ruleForm.project_name" :disabled="true">
            <template slot="prepend">案件名</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
