<script>
import { getDeliveryDataOne } from "@/api/delivery";
import {
  postAcceptanceData,
  putAcceptanceData,
  getAcceptanceDataOne,
} from "@/api/acceptance";
import { Message } from "element-ui";

export default {
  name: "AcceptanceForm",
  props: {
    cAcceptanceForm: {
      type: Object,
      default: function () {
        return {
          project_name: "",
          project_code: "",
          customer_name: "",
          estimate_code: "",
          delivery_code: "",
          acceptance_code: "",
          method: "",
        };
      },
    },
  },
  data() {
    return {
      ruleForm: {
        acceptance_code: this.cAcceptanceForm.acceptance_code,
        delivery_code: this.cAcceptanceForm.delivery_code,
        project_name: this.cAcceptanceForm.project_name,
        project_code: this.cAcceptanceForm.project_code,
        estimate_code: this.cAcceptanceForm.estimate_code,
        customer_name: this.cAcceptanceForm.customer_name,
        deliverables1: "",
        deliverables2: "",
        deliverables3: "",
        quantity1: "",
        quantity2: "",
        quantity3: "",
        memo1: "",
        memo2: "",
        memo3: "",
        remarks: "",
      },
      rules: {
        customer_name: [
          {
            required: true,
            message: "得意先名を入力してください",
            trigger: "change",
          },
        ],
        CustomerAddress: [
          {
            required: true,
            message: "得意先住所を入力してください",
            trigger: "blur",
          },
        ],
        Work: [
          {
            required: true,
            message: "作業内容を入力してください",
            trigger: "blur",
          },
        ],
        WorkTime: [
          {
            required: true,
            message: "作業期間を入力してください",
            trigger: "blur",
          },
        ],
        PersonnelI: [
          {
            required: true,
            message: "主任担当者(甲)を入力してください",
            trigger: "blur",
          },
        ],
        PersonnelII: [
          {
            required: true,
            message: "主任担当者(乙)を入力してください",
            trigger: "blur",
          },
        ],
        Deliverables: [
          {
            required: true,
            message: "成果物を入力してください",
            trigger: "blur",
          },
        ],
        DeliverableSpace: [
          {
            required: true,
            message: "納入場所を入力してください",
            trigger: "blur",
          },
        ],
        Commission: [
          {
            required: true,
            message: "委託料を入力してください",
            trigger: "blur",
          },
        ],
        PaymentDate: [
          {
            required: true,
            message: "支払期日を入力してください",
            trigger: "blur",
          },
        ],
        AcceptanceConditions: [
          {
            required: true,
            message: "検収条件を入力してください",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {
    if (this.cAcceptanceForm.method === "post") {
      this.getDeliveryDataOne();
    } else if (this.cAcceptanceForm.method === "put") {
      this.getAcceptanceDataOne();
    }
  },
  methods: {
    postAcceptanceData() {
      let values = {};
      values["acceptance_code"] = this.ruleForm.acceptance_code;
      values["delivery_code"] = this.ruleForm.delivery_code;
      values["project_name"] = this.ruleForm.project_name;
      values["project_code"] = this.ruleForm.project_code;
      values["estimate_code"] = this.ruleForm.estimate_code;
      values["customer_name"] = this.ruleForm.customer_name;
      values["deliverables1"] = this.ruleForm.deliverables1;
      values["deliverables2"] = this.ruleForm.deliverables2;
      values["deliverables3"] = this.ruleForm.deliverables3;
      values["quantity1"] = this.ruleForm.quantity1;
      values["quantity2"] = this.ruleForm.quantity2;
      values["quantity3"] = this.ruleForm.quantity3;
      values["memo1"] = this.ruleForm.memo1;
      values["memo2"] = this.ruleForm.memo2;
      values["memo3"] = this.ruleForm.memo3;
      values["remarks"] = this.ruleForm.remarks;
      values["created_by"] = this.$store.getters.full_name;
      postAcceptanceData(values)
        .then((res) => {
          if (res.data.status === 1) {
            this.$emit("submit");
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
          } else {
            this.$emit("cancel");
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    putAcceptanceData() {
      let values = {};
      values["acceptance_code"] = this.ruleForm.acceptance_code;
      values["delivery_code"] = this.ruleForm.delivery_code;
      values["project_name"] = this.ruleForm.project_name;
      values["project_code"] = this.ruleForm.project_code;
      values["estimate_code"] = this.ruleForm.estimate_code;
      values["customer_name"] = this.ruleForm.customer_name;
      values["deliverables1"] = this.ruleForm.deliverables1;
      values["deliverables2"] = this.ruleForm.deliverables2;
      values["deliverables3"] = this.ruleForm.deliverables3;
      values["quantity1"] = this.ruleForm.quantity1;
      values["quantity2"] = this.ruleForm.quantity2;
      values["quantity3"] = this.ruleForm.quantity3;
      values["memo1"] = this.ruleForm.memo1;
      values["memo2"] = this.ruleForm.memo2;
      values["memo3"] = this.ruleForm.memo3;
      values["remarks"] = this.ruleForm.remarks;
      values["modified_by"] = this.$store.getters.full_name;
      putAcceptanceData(values)
        .then((res) => {
          if (res.data.status === 1) {
            this.$emit("submit");
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
          } else {
            this.$emit("cancel");
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getAcceptanceDataOne() {
      let values = {};
      values["acceptance_code"] = this.cAcceptanceForm.acceptance_code;
      getAcceptanceDataOne(values)
        .then((res) => {
          this.ruleForm.acceptance_code = res.data.data[0].acceptance_code;
          this.ruleForm.delivery_code = res.data.data[0].delivery_code;
          this.ruleForm.project_name = res.data.data[0].project_name;
          this.ruleForm.project_code = res.data.data[0].project_code;
          this.ruleForm.estimate_code = res.data.data[0].estimate_code;
          this.ruleForm.customer_name = res.data.data[0].customer_name;
          this.ruleForm.deliverables1 = res.data.data[0].deliverables1;
          this.ruleForm.deliverables2 = res.data.data[0].deliverables2;
          this.ruleForm.deliverables3 = res.data.data[0].deliverables3;
          this.ruleForm.quantity1 = res.data.data[0].quantity1;
          this.ruleForm.quantity2 = res.data.data[0].quantity2;
          this.ruleForm.quantity3 = res.data.data[0].quantity3;
          this.ruleForm.memo1 = res.data.data[0].memo1;
          this.ruleForm.memo2 = res.data.data[0].memo2;
          this.ruleForm.memo3 = res.data.data[0].memo3;
          // this.ruleForm.remarks = res.data.data[0].remarks
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getDeliveryDataOne() {
      let values = {};
      values["delivery_code"] = this.cAcceptanceForm.delivery_code;
      getDeliveryDataOne(values)
        .then((res) => {
          this.ruleForm.deliverables1 = res.data.data[0].deliverables1;
          this.ruleForm.deliverables2 = res.data.data[0].deliverables2;
          this.ruleForm.deliverables3 = res.data.data[0].deliverables3;
          this.ruleForm.quantity1 = res.data.data[0].quantity1;
          this.ruleForm.quantity2 = res.data.data[0].quantity2;
          this.ruleForm.quantity3 = res.data.data[0].quantity2;
          this.ruleForm.memo1 = res.data.data[0].memo1;
          this.ruleForm.memo2 = res.data.data[0].memo2;
          this.ruleForm.memo3 = res.data.data[0].memo3;
          // this.ruleForm.remarks = res.data.data[0].remarks
        })
        .catch((err) => {
          console.log(err);
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.cAcceptanceForm.method === "put") {
            this.putAcceptanceData();
          } else {
            this.postAcceptanceData();
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.$emit("cancel");
    },
  },
};
</script>

